<template>
	<div class="add_carousel">
		<el-form label-position="right" :rules="rules" ref="form" label-width="120px" :model="form">
			<el-row>
				<el-col :span="7">
				  	<el-form-item label="" prop="carousel_name">
				    	<el-input size="medium" v-model="form.carousel_name" placeholder=""></el-input>
				  	</el-form-item>
			  	</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="" prop="carousel_location">
						<el-select size="medium" v-model="form.carousel_location" placeholder="">
							<el-option label="" value="2"></el-option>
							<el-option label="" value="1"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col>
					<el-form-item label="" prop="dialogImageUrl">
						<el-upload
						  action="https://tony"
						  list-type="picture-card"
						  :on-preview="handlePictureCardPreview"
						  :on-remove="handleRemove"
						  :limit="1"
  						  :on-exceed="handleExceed">
						  <i class="el-icon-plus"></i>
						</el-upload>
						<el-dialog :visible.sync="dialogVisible" size="tiny">
						  <img width="100%" :src="form.dialogImageUrl" alt="">
						</el-dialog>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="" prop="carousel_url">
						<el-input size="medium" v-model="form.carousel_url" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="">
						<el-input size="medium" v-model="form.carousel_order" placeholder="1-5"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="">
						<el-switch v-model="form.carousel_switch" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item>
						<el-button type="primary" size="small" @click="submit('form')"></el-button>
						<el-button size="small" @click="back"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "add_carousel",
		data(){
			return{
				form: {
					carousel_name     : '',			//
					carousel_location : "",			//
					dialogImageUrl    : '',			//
					carousel_url      : "",			//
					carousel_order    : "",			//
					carousel_switch   : true,		//
				},
				rules: {
					carousel_name     : [{ required: true, message: '', trigger: 'blur' }],
					carousel_location : [{ required: true, message: '', trigger: 'blur' }],
					dialogImageUrl    : [{ required: true, message: '', trigger: 'blur' }],
					carousel_url      : [{ required: true, message: '', trigger: 'blur' }],
				},
        		dialogVisible: false
			}
		},
		methods:{
			submit(form){
				this.$refs[form].validate((valid) => {
		          	if (valid) {
		           	 	console.log("")
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			},
			back(){
				this.$router.go(-1)
			},
			handleRemove(file, fileList) {
		        console.log(file, fileList);
		    },
		    handlePictureCardPreview(file) {
		        this.form.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		    },
		    //
		    handleExceed(files, fileList) {
		        this.$message.warning(` 5 ${files.length + fileList.length} `);
		    }
		}
	})
</script>

<style>
</style>


