<template>
	<div class="add_qust_ansr">
		<el-form label-position="right" :rules="rules" ref="form" label-width="120px" :model="form">
			<el-row>
				<el-col :span="7">
				  	<el-form-item label="" prop="QStitle">
				    	<el-input size="medium" v-model="form.QStitle" placeholder=""></el-input>
				  	</el-form-item>
			  	</el-col>
			</el-row>
			<el-row>
				<el-col :span="7">
					<el-form-item label="" prop="location">
						<el-select size="medium" v-model="form.location" placeholder="">
							<el-option label="" value="2"></el-option>
							<el-option label="" value="1"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="10">
					<el-form-item prop="QSdesc" label="">
						<el-input type="textarea" :rows="5" resize="none" v-model="form.QSdesc" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<!---->
			<el-form label-position="right" :rules="rules_1" ref="form_1" label-width="120px" :model="form_1">
				<el-row>
					<el-col :span="22">
						<el-card class="box-card">
							<div slot="header" class="clearfix">
							    <el-button type="default" icon="el-icon-circle-plus-outline" @click="addQuestion"></el-button>
							    <el-button type="default" icon="el-icon-view" @click="preview"></el-button>
							</div>
							<!---->
						  	<div v-if="!show_flag" class="addNew_question">
						  		<el-row>
									<el-col :span="12">
									  	<el-form-item label="" prop="addQStitle">
									    	<el-input size="medium" v-model="form_1.addQStitle" placeholder=""></el-input>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item label="" prop="addQSdesc">
									    	<el-input size="medium" v-model="form_1.addQSdesc" placeholder=""></el-input>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
										<el-form-item label="" prop="addQStype">
											<el-select size="medium" v-model="form_1.addQStype" placeholder="">
												<el-option label="" value="2"></el-option>
												<el-option label="" value="1"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item label="">
									    	<el-input size="medium" v-model="form_1.addQSsort" placeholder="0"></el-input>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row v-for="(item,index) in form_1.selects" :key="index" >
									<el-col :span="12">
									  	<el-form-item :prop="'selects.' + index + '.value'" 
									  		:rules="{required: true, message: '', trigger: 'blur'}">
									    	<el-input size="medium" v-model="form_1.selects[index].value" placeholder="" style="width: calc(100% - 60px);"></el-input>
									    	<el-button size="medium" @click.prevent="removeSelect(index)" icon="el-icon-delete"></el-button>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item>
									    	 <el-button size="medium" icon="el-icon-circle-plus" @click="addSelect"></el-button>
									  	</el-form-item>
								  	</el-col>
								</el-row>
								<el-row>
									<el-col :span="12">
									  	<el-form-item>
									    	 <el-button size="medium" type='warning' @click="addSure('form_1')"></el-button>
									    	 <el-button size="medium" @click="addCancle"></el-button>
									  	</el-form-item>
								  	</el-col>
								</el-row>
						  	</div>
						  	<!---->
						  	<div v-else class="show_question">
						  		<div v-for="(item,index) in 3" class="question_item">
						  			<p>{{index+1}}</p>
						  			<div v-if="index%2==0" class="answers_select">
						  				<p><input type="radio" name="radio" />5%</p>
						  				<p><input type="radio" name="radio" />15%</p>
						  				<p><input type="radio" name="radio" />25%</p>
						  			</div>
						  			<div v-else="" class="answers_select">
						  				<p><input type="checkbox" name="" />5%</p>
						  				<p><input type="checkbox" name="" />15%</p>
						  				<p><input type="checkbox" name="" />25%</p>
						  			</div>
						  		</div>
						  	</div>
						</el-card>	
					</el-col>
				</el-row>
			</el-form>
			<br />
			<br />
			<!---->
			<el-row>
				<el-col>
					<el-form-item>
						<el-button type="primary" @click="submit('form')"></el-button>
						<el-button @click="back"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "add_qust_ansr",
		data(){
			return{
				form:{
					QStitle : "",			//
					location: "",			//
					QSdesc  : "",			//
				},
				form_1:{
					addQStitle: "",			//
					addQSdesc : "",			//
					addQStype : "",			//
					addQSsort : "",			//
					selects: [				//
						{ value: '' },
					],
				},
				rules: {
					QStitle   : [{ required: true, message: '', trigger: 'blur' }],
					location  : [{ required: true, message: '', trigger: 'blur' }],
					QSdesc    : [{ required: true, message: '', trigger: 'blur' }],
				},
				rules_1: {
					addQStitle: [{ required: true, message: '', trigger: 'blur' }],
					addQStype : [{ required: true, message: '', trigger: 'blur' }],
				},
				show_flag: true,			//
			}
		},
		methods:{
			//
			addQuestion(){
				this.show_flag = false;
			},
			//
			preview(){
				this.$router.push({path: "qust_ansr_preview"})
			},
			//
			addSelect(){
				this.form_1.selects.push({value: ''});
			},
			//
			removeSelect(index){
	          	this.form_1.selects.splice(index, 1)
			},
			//
			addSure(form_1){
				console.log(this.form)
				this.$refs[form_1].validate((valid) => {
		          	if (valid) {
		           	 	console.log("")
		           	 	this.show_flag = true;
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			},
			//
			addCancle(){
				this.show_flag = true;
			},
			submit(form){
				this.$refs[form].validate((valid) => {
		          	if (valid) {
		           	 	console.log("")
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			},
			back(){
				this.$router.go(-1)
			},
		}
	})
</script>

<style scoped="scoped" lang="less">
	.add_qust_ansr .el-card.box-card{margin-left: 45px;}
	.add_qust_ansr .box-card .el-select{width: 100%;}
	
	.add_qust_ansr{
		.box-card {
			.show_question{
				height: 470px;
				overflow-y: auto;
				margin-left: 50px;
				.question_item{
					padding: 15px 20px;
					margin-bottom: 25px;
					width: calc(~"100% - 100px");
					background: #f5f5f5;
					border-radius: 5px;
					.answers_select{
						p{
							margin-top: 15px ;
							input{margin-right: 10px; margin-left: 25px;}
						}
					}
				}
			}
		}
	} 
</style>


