<template>
	<div class="party_branch">
		<el-form :inline="true" :rules="rules" :model="formInline" class="demo-form-inline">
			<el-row>
				<!---->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.partyBranch_name" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.admin_name" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="4">
					<el-form-item prop="date1">
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line"></el-col>
				<el-col :span="4">
					<el-form-item prop="date2">
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="info" @click="search"></el-button>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2" :offset="2">
					<el-form-item>
						<el-button type="primary" size="small" @click="addGroup"></el-button>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2">
					<el-form-item>
						<el-button type="warning" size="small" @click="batchRemove"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!---->
		<div class="user_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="" width="260" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="edit(scope.$index, scope)"></el-button>
						<el-button type="text" size="small" @click="detail(scope.$index)"></el-button>
						<el-button type="text" size="small" @click="addBlacklist(scope.$index)"></el-button>
						<el-button type="text" size="small" @click="seeAll(scope.$index)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "party_branch",
		data(){
			return{
				formInline: {
					partyBranch_name: "",			//
					date1           : "",			//
					date2           : "",			//
					admin_name      : "",			//
				},
				rules: {
					date1: [{ type: 'date', required: true, message: '', trigger: 'change' }],
					date2: [{ type: 'date', required: true, message: '', trigger: 'change' }],
				},
				tableHeade: [
					{name: "ID",	prop: "id",  	width: 80, tooltip: false},
					{name: "", 	prop: "name", 	width: "", tooltip: true},
					{name: "",	prop: "time", 	width: 180, tooltip: false},
					{name: "",	prop: "pNum",	width: 100, tooltip: false},
					{name: "",  	prop: "admin", 	width: 180, tooltip: false},
				],
				tableData: [
				{ id: 77, name: '', time: '2017-12-07', pNum:"25", admin: ""},
				{ id: 77, name: '', time: '2017-12-07', pNum:"25", admin: ""},
				{ id: 77, name: '', time: '2017-12-07', pNum:"25", admin: ""},
				{ id: 77, name: '', time: '2017-12-07', pNum:"25", admin: ""},
				{ id: 77, name: '', time: '2017-12-07', pNum:"25", admin: ""},
				],
			}
		},
		methods:{
			//
			search() {
				console.log('search!');
			},
			//
			addGroup(){
				this.$router.push({path: "/partyBranch_add"})
			},
			//
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//
			batchRemove(){
				var that = this;
				that.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
				})
			},
			//
			edit(index,scope){
				this.$router.push({path: "/partyBranch_add", query:{reName: ""}})
			},
			//
			detail(index){
				this.$router.push({path: "partyBranch_detail"})
			},
			//
			addBlacklist(index){
				var that = this;
				that.$confirm('', '', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(()=>{});
			},
			//
			seeAll(index){
				this.$router.push({path: "partyBranch_peoples"})
			},
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		}
	})
</script>

<style scoped lang="less">
	.party_branch {
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


