<template>
	<div class="post_comment">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.postName" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.userName" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="4">
					<el-form-item>
						<el-input size="medium" v-model="formInline.comment" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="3">
					<el-form-item>
						<el-button size="medium" type="primary" @click="search"></el-button>
					</el-form-item>
				</el-col>
				<el-col :span="3" :offset="3">
					<el-form-item>
						<el-button size="medium" type="primary" plain @click="all">1229</el-button>
					</el-form-item>
				</el-col>
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="warning" plain @click="batch_delet"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!---->
		<el-table ref="multipleTable" border :data="tableData" tooltip-effect="dark" style="width: 95%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55"></el-table-column>
			<el-table-column align="center" prop="comment_user" label="" width="140"></el-table-column>
			<el-table-column align="center" prop="post_name" label="" width="200"></el-table-column>
			<el-table-column align="center" prop="post_time" label="" width="160"></el-table-column>
			<el-table-column align="center" prop="post_content" label="" show-overflow-tooltip></el-table-column>
			<el-table-column align="center" fixed="right" label="" width="100">
		      	<template slot-scope="scope">
			        <el-button type="text" size="small" @click="delet(scope.row)"></el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "post_comment",
		data() {
			return {
				formInline: {
		          	postName: '',
		          	userName: '',
		          	comment : '',
		        },
		        tableData: [
					{comment_user: "", post_name:"", post_time:"2017-12-07", post_content: 999},
					{comment_user: "", post_name:"", post_time:"2017-12-07", post_content: 999},
					{comment_user: "", post_name:"", post_time:"2017-12-07", post_content: 999},
					{comment_user: "", post_name:"", post_time:"2017-12-07", post_content: 999},
				]
			}
		},
		methods: {
			//
			search() {
				console.log('submit!');
			},
			//
			all(){
				
			},
			//
			batch_delet(){
				
			},
			//
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//
			delet(index){
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	confirmButtonText: '',
		          	cancelButtonText: '',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		}
	})
</script>

<style>

</style>


