<template>
	<div class="add_post">
		<el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="120px">
			<el-row>
				<el-col >
					<el-form-item label="" prop="title">
						<el-input v-model="form.title" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>	
				<el-col>
					<el-form-item label="" prop="classify">
						<el-select v-model="form.classify" placeholder="">
							<el-option label="" value="shanghai"></el-option>
							<el-option label="" value="beijing"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<!---->
			<el-row>
				<UE_editor :defaultMsg='defaultMsg' :config="config" ref="ue"></UE_editor>
			</el-row>
			<el-form-item class="btns">
			    <el-button size="medium" type="primary" @click="submit('form')"></el-button>
			    <el-button size="medium" @click="back('form')"></el-button>
  			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	import UE_editor from '@/components/template/editor.vue';
	export default({
		name: "add_post",
		data() {
			return {
				form: {
					title: "",
					classify: "",
					editor_content: "",
				},
				defaultMsg: '',
		        config: {
		          	initialFrameWidth  : 800,		//
		          	initialFrameHeight : 350,		//
		          	indentValue		   : '2em',		//
		          	wordCount          : false,		//
		          	elementPathEnabled :false,		//
		          	zIndex             : 9,			//
		        },
		        rules: {
		          	title: [
		            	{ required: true, message: '', trigger: 'blur' }
		          	],
		          	classify: [
		            	{ required: true, message: '', trigger: 'change' }
		          	]
		        }
			}
		},
		components: {UE_editor},
		created() {

		},
		methods: {
			//
			submit(form){
				var that = this;
				//
				that.form.editor_content = this.$refs.ue.getUEContent();
				//
				that.$refs[form].validate((valid) => {
	          		if (valid) {
	            		console.log(this.form)
	          		} else {
	            		console.log('!!');
	            		return false;
	          		}
        		});
			},
			//
			back(){
				this.$router.go(-1)
			}
		}
	})
</script>

<style type="text/css">
	.add_post #editor{ margin-left: 120px; }
</style>
<style scoped="scoped" lang="less">
	.btns{
		margin-left: 120px;
		margin-top: 30px;
	}
</style>


