<template>
	<div class="content_classify">
		<el-button type="primary" size="small" @click="addNewClassify"></el-button><br /><br /><br />
		<el-table ref="multipleTable" border :data="tableData" tooltip-effect="dark" style="width: 90%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55"></el-table-column>
			<el-table-column align="center" prop="sort_name" label="" width="160"></el-table-column>
			<el-table-column align="center" prop="sort_describe" label="" show-overflow-tooltip></el-table-column>
			<el-table-column align="center" prop="post_num" label="" width="120"></el-table-column>
			<el-table-column align="center" fixed="right" label="" width="100">
		      	<template slot-scope="scope">
			        <el-button type="text" size="small" @click="edit(scope.row)"></el-button>
			        <el-button type="text" size="small" @click="delet(scope.row)"></el-button>
		      	</template>
		    </el-table-column>
		</el-table>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default {
		name: "ContentClassify",
		data() {
			return {
				tableData: [
					{sort_name: "", sort_describe:"", post_num: 999},
					{sort_name: "", sort_describe:"", post_num: 999},
					{sort_name: "", sort_describe:"", post_num: 999},
				]
			}
		},
		methods: {
			//
			addNewClassify() {
				this.$router.push({path: "add_classify"})
			},
			//
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//
			edit(){
				this.$router.push({path: "add_classify", query:{reName: ""}})
			},
			//
			delet(index){
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	confirmButtonText: '',
		          	cancelButtonText: '',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		}
	}
</script>

<style>

</style>


