<template>
	<div class="blacklist">
		<!---->
		<div class="top_fun">
			<el-form :inline="true" :rules="rules" :model="formInline" class="demo-form-inline">
				<el-row>
					<!---->
					<el-col :span="3">
						<el-form-item>
							<el-select size="medium" v-model="formInline.partyBranch" placeholder="">
								<el-option label="" value="2"></el-option>
								<el-option label="" value="1"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="3">
						<el-form-item>
							<el-select size="medium" v-model="formInline.approval" placeholder="">
								<el-option label="" value="0"></el-option>
								<el-option label="" value="2"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<!--ID-->
					<el-col :span="3">
						<el-form-item>
							<el-input size="medium" v-model="formInline.userId" placeholder="ID"></el-input>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="3">
						<el-form-item>
							<el-input size="medium" v-model="formInline.userName" placeholder=""></el-input>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="2">
						<el-form-item>
							<el-button size="medium" type="info" @click="search"></el-button>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="2" :offset="6">
						<el-form-item>
							<el-button type="warning" size="small" @click="batchRemove"></el-button>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</div>
		<!---->
		<div class="user_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="" width="220" fixed="right">
					<template slot-scope="scope">
						<!--<el-button type="text" size="small" @click="edit(scope.$index, scope)"></el-button>-->
						<el-button type="text" size="small" @click="detail(scope.$index)"></el-button>
						<el-button type="text" size="small" @click="remove(scope.$index)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name:"blacklist",
		data(){
			return{
				formInline: {
					partyBranch : '',			//
					approval    : "",			//
					userId      : "",			//id
					userName    : "",			//
				},
				rules: {
					date1: [{ type: 'date', required: true, message: '', trigger: 'change' }],
					date2: [{ type: 'date', required: true, message: '', trigger: 'change' }],
				},
				tableHeade: [
					{name: "ID",    prop: "id",      width: 80, tooltip: false},
					{name: "", prop: "phone",    width: 140, tooltip: false},
					{name: "",      prop: "nickName", width: 160, tooltip: false},
					{name: "",      prop: "name",     width: 100, tooltip: false},
					{name: "",      prop: "sex",      width: 100, tooltip: false},
					{name: "",   prop: "approve",  width: 120, tooltip: false},
					{name: "", prop: "partyBranch",  width: "", tooltip: true},
				],
				tableData: [
					{ id: 77, phone: '18937107705', nickName: '', name: 'tony', sex:"", approve: "", partyBranch: "" },
					{ id: 77, phone: '18937107705', nickName: '', name: 'tony', sex:"", approve: "", partyBranch: "" },
					{ id: 77, phone: '18937107705', nickName: '', name: 'tony', sex:"", approve: "", partyBranch: "" },
					{ id: 77, phone: '18937107705', nickName: '', name: 'tony', sex:"", approve: "", partyBranch: "" },
					{ id: 77, phone: '18937107705', nickName: '', name: 'tony', sex:"", approve: "", partyBranch: "" },
				],
			}
		},
		methods: {
			//
			search() {
				console.log('search!');
			},
			//
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//
			batchRemove(){
				var that = this;
				that.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
				}).catch(() => {});
			},
			//
			edit(index,scope){
				console.log("",index, scope)
			},
			//
			detail(index){
				this.$router.push({path: "user_detail"})
			},
			//
			remove(index){
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		}
	})
</script>

<style scoped="scoped" lang="less">
	.blacklist{
	 	.top_fun{
	 		margin-bottom: 20px;
	 	}
	 }
</style>


