<template>
	<div class="partyBranch_add">
		<el-form  :rules="rules" :model="formInline" ref="formInline" label-width="160px" class="demo-form-inline">
			<el-row>
				<el-col :span="8">
					<el-form-item label=""prop="partyBranchName">
						<el-input size="medium" v-model="formInline.partyBranchName" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="adminName">
						<el-input size="medium" v-model="formInline.adminName" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col v-if="addAdmin" :span="2" :pull="2">
					<el-form-item>
						<el-button type="text" size="medium" @click="addAdmin=!addAdmin"></el-button>
					</el-form-item>
				</el-col>	
			</el-row>
			<el-row v-if="!addAdmin">
				<el-col :span="8">
					<el-form-item prop="adminName_2">
						<el-input size="medium" v-model="formInline.adminName_2" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-form-item>
				<el-button type="primary" size="medium" @click="onSubmit('formInline')"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default({
		name: "partyBranch_add",
		data() {
			return {
				addAdmin  : true,
				formInline: {
					partyBranchName: "",	//
					adminName      : "",	//
					adminName_2    : "",	//
				},
				rules: {
					partyBranchName : [{ required: true, message: '', trigger: 'blur' }],
					adminName       : [{ required: true, message: '', trigger: 'blur' }],
					adminName_2     : [{ required: true, message: '', trigger: 'blur' }],
				}
			}
		},
		methods:{
			//
			onSubmit(form){
				this.$refs[form].validate((valid) => {
		          	if (valid) {
		           	 	console.log("")
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			}
		}
	})
</script>

<style>
	
</style>


