<template>
	<div class="add_admin">
		<el-form label-position="right" label-width="120px" :model="formInline" :rules="rules" ref="formInline">
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="account_number">
				    	<el-input v-model="formInline.account_number" placeholder="612"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="set_password">
				    	<el-input type="password" v-model="formInline.set_password" placeholder="612"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="agree_password">
				    	<el-input type="password" v-model="formInline.agree_password" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="admin_name">
				    	<el-input v-model="formInline.admin_name" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="region">
					    <el-select v-model="formInline.region" placeholder="" size="large">
					      <el-option label="" value="0"></el-option>
					      <el-option label="1" value="1"></el-option>
					      <el-option label="2" value="2"></el-option>
					    </el-select>
					</el-form-item>
				</el-col>
				<el-col :span="2" :offset="1">
					<el-button size="medium" type="text" @click="addGroup"></el-button>
				</el-col>
			</el-row>
			<el-row>
				<el-col>
					<el-form-item>
					    <el-button type="primary" size="medium" @click="submitForm('formInline')"></el-button>
					    <el-button size="medium" @click="resetForm('formInline')"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default({
		name : "add_admin" ,
		data(){
			return {
				formInline:{
					account_number:"",
					set_password : "" ,
					agree_password : "" ,
					admin_name : "" ,
					region : "" ,
				},
				rules:{
					account_number: [
			            { required: true, message: '', trigger: 'blur' },
			            { min: 6, max: 12, message: ' 6 12 ', trigger: 'blur' }
			        ],
			        set_password: [
			            { required: true, message: '', trigger: 'blur' },
			            { min: 6, max: 12, message: ' 6 12 ', trigger: 'blur' }
			        ],
			        agree_password: [
			            { required: true, message: '', trigger: 'blur' },
			            { min: 6, max: 12, message: ' 6 12 ', trigger: 'blur' }
			        ],
			        admin_name: [
			            { required: true, message: '', trigger: 'blur' },
			        ],
			        region: [
			            { required: true, message: '', trigger: 'change' }
			        ],
				}
			}
		},
		methods:{
			addGroup(){
				this.$router.push({path: "add_group"})
			},
			submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		            alert('submit!');
		          } else {
		            console.log('error submit!!');
		            return false;
		          }
		        });
		    },
		    resetForm(formName) {
		        this.$refs[formName].resetFields();
		    }
		}
	})
</script>

<style type="text/css">
	.add_admin .el-form-item .el-select.el-select--large{width: 100%;}
</style>
<style scoped="scoped" lang="less">
	.add_admin{
		margin-left: 60px;
	}
</style>


