<template>
	<div class="admin_list">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<!---->
				<el-col :span="3">
					<el-form-item label="">
					    <el-select v-model="formInline.region" placeholder="" size="medium">
					      	<el-option label="" value="0"></el-option>
					      	<el-option label="" value="1"></el-option>
					      	<el-option label="" value="2"></el-option>
					      	<el-option label="1" value="3"></el-option>
					      	<el-option label="2" value="4"></el-option>
					    </el-select>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="3">
					<el-form-item label="">
				    	<el-input v-model="formInline.admin_name" placeholder="" size="medium"></el-input>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="3">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line" :span="1"></el-col>
				<el-col :span="3">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2" :offset="2">
					<el-form-item>
					    <el-button type="info" size="medium" @click="onSearch"></el-button>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2">
					<el-form-item>
					    <el-button type="primary" size="medium" @click="addAdmin"></el-button>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2" :offset="1">
					<el-form-item>
						<el-button type="warning" size="medium" @click="batchRemove"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<!---->
		<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width:100%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
		    <el-table-column v-for="item in tableHeader" :key="item.prop" :prop="item.prop" :label="item.name" :show-overflow-tooltip="item.tooltip" :width="item.width" align="center"></el-table-column>
		    <el-table-column align="center" label="" width="260" fixed="right">
				<template slot-scope="scope">
					<el-button type="text" size="small" @click="edit(scope.$index, scope)"></el-button>
					<el-button type="text" size="small" @click="disabled(scope.$index)"></el-button>
					<el-button type="text" size="small" @click="delet(scope.$index)"></el-button>
				</template>
			</el-table-column>
		</el-table>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name : "admin_list" ,
		data(){
			return {
			    formInline: {
			        admin_name: '',
			        region: '',
			        date1: '',
		      		date2: '',
		        },
		        tableHeader:[
		      		{ name:"" , prop:"admin_name"   , width:140 , tooltip:false},
		      		{ name:"" , prop:"admin_num"    , width:160 , tooltip:false},
		      		{ name:""   , prop:"admin_power"  , width:140 , tooltip:false},
		      		{ name:""   , prop:"add_time"     , width:"" ,  tooltip:true},
		      		{ name:""      , prop:"admin_statue" , width:120 , tooltip:false},
		        ],
		        tableData: [
		      		{admin_name: '',admin_num:"admin",admin_power:"",add_time: '2016-05-03',admin_statue:''},
		      		{admin_name: '',admin_num:"admin",admin_power:"",add_time: '2016-05-03',admin_statue:''},
		      		{admin_name: '',admin_num:"admin",admin_power:"",add_time: '2016-05-03',admin_statue:''},
		      		{admin_name: '',admin_num:"admin",admin_power:"",add_time: '2016-05-03',admin_statue:''},
		      		{admin_name: '',admin_num:"admin",admin_power:"",add_time: '2016-05-03',admin_statue:''},
		      		{admin_name: '',admin_num:"admin",admin_power:"",add_time: '2016-05-03',admin_statue:''},
		      		{admin_name: '',admin_num:"admin",admin_power:"",add_time: '2016-05-03',admin_statue:''},
			    ],
			}
		},
		methods:{
			//
			onSearch() {
		        console.log('');
		    },
		    //
		    addAdmin(){
		    	this.$router.push({path:"add_admin"})
		    },
		    //
		    handleSelectionChange(val) {
		        this.multipleSelection = val;
		        console.log(val)
		    },
		    //
			batchRemove(){
				var that = this;
				that.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
				})
			},
		    //
			edit(index,scope){
//				console.log("",index, scope)
				this.$router.push({path:"add_admin", query:{reName: ""}})
			},
			//
			disabled(index){
				var that = this;
				that.$confirm('', '', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(()=>{});
			},
			//
			delet(index){
				var that = this;
				that.$confirm('', '', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(()=>{});
			},
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		},
	})
</script>

<style scoped="scoped" lang="less">
	.admin_list {
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


