<template>
	<div class="index">
		<!---->
		<div class="top_fun">
			<el-form :inline="true" :rules="rules" :model="formInline" class="demo-form-inline">
				<el-row>
					<!---->
					<el-col :span="3">
						<el-form-item>
							<el-select size="medium" v-model="formInline.classify" placeholder="">
								<el-option label="" value="2"></el-option>
								<el-option label="" value="1"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="3">
						<el-form-item>
							<el-select size="medium" v-model="formInline.attr_classify" placeholder="">
								<el-option label="" value="0"></el-option>
								<el-option label="" value="2"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="3">
						<el-form-item>
							<el-select size="medium" v-model="formInline.participate" placeholder="">
								<el-option label="" value="0"></el-option>
								<el-option label="" value="2"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="4">
						<el-form-item prop="date1">
							<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
						</el-form-item>
					</el-col>
					<el-col class="line"></el-col>
					<el-col :span="4">
						<el-form-item prop="date2">
							<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="3">
						<el-form-item>
							<el-input size="medium" v-model="formInline.posts_name" placeholder=""></el-input>
						</el-form-item>
					</el-col>
					<!---->
					<el-col :span="2">
						<el-form-item>
							<el-button size="medium" type="info" @click="search"></el-button>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<!----->
					<el-col>
						<el-form-item>
							<el-button type="primary" size="small" @click="addNew"></el-button>
							<el-button type="warning" size="small" @click="batchRemove"></el-button>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</div>
		<!---->
		<div class="user_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="" width="220" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="edit(scope.$index, scope)"></el-button>
						<el-button type="text" size="small" @click="audit(scope.$index)"></el-button>
						<el-button type="text" size="small" @click="delet(scope.$index)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default {
		name: 'Index',
		data() {
			return {
				formInline: {
					classify     : '',			//
					attr_classify: "",			//
					participate  : "",			//
					date1        : "",			//
					date2        : "",			//
					posts_name   : "",			//
				},
				rules: {
					date1: [{ type: 'date', required: true, message: '', trigger: 'change' }],
					date2: [{ type: 'date', required: true, message: '', trigger: 'change' }],
				},
				tableHeade: [
					{name: "ID",  prop: "id",     width: 120, tooltip: false},
					{name: "", prop: "name",   width: "",  tooltip: true},
					{name: "", prop: "date",   width: 160, tooltip: false},
					{name: "", prop: "locate", width: 120, tooltip: false},
					{name: "",    prop: "audit",  width: 120, tooltip: false},
					{name: "",  prop: "joinNum", width: 120, tooltip: false},
					{name: "",  prop: "browse",  width: 120, tooltip: false},
				],
				tableData: [
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				{ id: 77, date: '2016-05-03', name: '', locate: '', audit:"", joinNum: 1123, browse: 99999 },
				],
			}
		},
		created() {
			this.$axios.get("index", {
				params: {
					name: "999"
				}
			}).then((res) => {
				console.log(res)
			}).catch((err) => {
				console.log(err)
			})
		},
		methods: {
			//
			search() {
				console.log('search!');
			},
			//
			addNew(){
				this.$router.push({path: "/add_post"})
			},
			//
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//
			batchRemove(){
				var that = this;
				that.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
				}).catch(()=>{})
			},
			//
			edit(index,scope){
				this.$router.push({path: "/add_post", query:{reName:""}})
			},
			//
			audit(index){
				console.log("",index)
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	confirmButtonText: '',
		          	cancelButtonText: '',
		          	closeOnClickModal: false,
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {
		        	that.$prompt('', '',{
		        		inputPattern:  /\S/,
		        		inputErrorMessage: "",
		        		closeOnClickModal: false
		        	}).then(({ value }) => {
			          	that.$message({
			            	type: 'success',
			            	message: ''
			          	});
			        }).catch(()=>{});         
		        });
			},
			//
			delet(index){
				console.log("",index)
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	confirmButtonText: '',
		          	cancelButtonText: '',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		}
	}
</script>

<style scoped lang="less">
	.index {
		.top_fun {
			.line {
				margin-right: 10px;
				height: 40px;
				width: 20px;
				text-align: center;
				line-height: 40px;
			}
		}
	}
</style>


