<template>
	<div class="carousel_list">
		<el-button size="medium" type="primary" @click="addNew"></el-button>
		<el-button size="medium" type="warning" @click="batchDel"></el-button>
		<br />
		<br />
		<br />
		<!---->
		<el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
			<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
			<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" 
				:prop="item.prop" :label="item.name" :width="item.width" 
				:show-overflow-tooltip="item.tooltip">
			</el-table-column>
			<el-table-column label="" width="100" align="center"> 
				<template slot-scope="scope"> 
					<img :src="scope.row.img" width="60" height="60"/>
				</template> 
			</el-table-column>
			<el-table-column label="" width="180" align="center">
				<template slot-scope="scope">
					<el-button size="mini" type="text" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button size="mini" type="text" @click="handleDelete(scope.$index, scope.row)"></el-button>
					<el-button size="mini" type="text" @click="useSwitch(scope.$index, scope.row)">/</el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default({
		name: "carousel_list",
		data() {
			return{
				tableHeade: [
					{name: "ID",   	prop: "id",      		width: 100,	tooltip: false},
					{name: "",	prop: "carousel_title", width: "", 	tooltip: true},
					{name: "",	prop: "show_location",	width: 140, tooltip: false},
					{name: "",   	prop: "sort",			width: 80, 	tooltip: false},
					{name: "",		prop: "status",			width: 100, tooltip: false},
					{name: "",	prop: "update_time",	width: 140, tooltip: false},
//					{name: "",		prop: "img",			width: 180, tooltip: false, scope: true},
				],
				tableData: [
					{
						id: "01", 
						carousel_title: '', 
						show_location: "", 
						sort:"1",
						status:"",
						img:"../../../../static/images/logo.png",
						update_time: '2016-05-02',
					},
					{
						id: "01", 
						carousel_title: '', 
						show_location: "", 
						sort:"1",
						status:"",
						img:"../../../../static/images/logo.png",
						update_time: '2016-05-02',
					},
					{
						id: "01", 
						carousel_title: '', 
						show_location: "", 
						sort:"1",
						status:"",
						img:"../../../../static/images/logo.png",
						update_time: '2016-05-02',
					},
				]
			}
		},
		methods: {
			//
			addNew() {
				this.$router.push({path: "add_carousel"})
			},
			//
			batchDel() {
				var that = this;
				that.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
				})
			},
			//
			handleSelectionChange(val) {
        		console.log(val)
      		},
      		//
			handleEdit(index, row) {
				console.log(index, row);
				this.$router.push({path: "add_carousel", query:{reName: ""}})
			},
			//
			handleDelete(index, row) {
				var that = this;
				console.log(index, row);
				that.$confirm('', '', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			},
			//
			useSwitch(index, row){
				var that = this;
				console.log(index, row);
			}
		}
	})
</script>

<style>

</style>


