<template>
	<div class="participan">
		<el-form class="demo-form-inline" label-width="140px">
			<el-row>
				<el-col :span="12">
					<el-form-item label="">
						<el-input v-model="contentTitle" readonly></el-input>
					</el-form-item>	
				</el-col>
			</el-row>
		</el-form>
		<br />
		<br />
		<!---->
		<div class="user_list">
			<el-table ref="multipleTable" border :data="tableData" tooltip-effect="dark" style="width: 90%">
				<el-table-column align="center" prop="name" label="" width="120"></el-table-column>
				<el-table-column align="center" prop="time" label="" show-overflow-tooltip></el-table-column>
			</el-table>
		</div>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name:"participan",
		data(){
			return{
				contentTitle: "1070 0.2% ",
				tableData: [
					{ name: 77, time: '2017-12-07'},
					{ name: 77, time: '2017-12-07'},
					{ name: 77, time: '2017-12-07'},
				],
			}
		},
		methods:{
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		}
	})
</script>

<style>
</style>


