<template>
	<div class="personal_ranking">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<el-col :span="4">
					<el-form-item>
						<p style="height: 35px; overflow: hidden;"></p>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="" v-model="showTime1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line"></el-col>
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="" v-model="showTime2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2">
					<el-form-item>
						<el-button type="warning" size="small" @click="openTime"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<!---->
				<el-col :span="3">
					<el-form-item>
						<el-select size="medium" v-model="formInline.partyBranch" placeholder="">
							<el-option label="" value="2"></el-option>
							<el-option label="" value="1"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<!--ID-->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.userID" placeholder="ID"></el-input>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.nickname" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="3">
					<el-form-item>
						<el-input size="medium" v-model="formInline.usercode" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line"></el-col>
				<el-col :span="4">
					<el-form-item>
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="info" @click="search"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!---->
		<el-table :data="tableData" style="width: 100%" :default-sort = "{prop: 'No', order: 'descending'}" >
		    <el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip" :sortable="item.sortable"></el-table-column>
				<el-table-column align="center" label="" width="220" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="detail(scope.$index)"></el-button>
						<el-button type="text" size="small" @click="remove(scope.$index)"></el-button>
						<el-button type="text" size="small" @click="toBlacklist(scope.$index)"></el-button>
					</template>
				</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default({
		name:"personal_ranking",
		data(){
			return{
				formInline: {
					partyBranch     : "",			//
					userID          : "",			//id
					nickname        : "",			//
					usercode        : "",			//
					date1           : "",			//
					date2           : "",			//
				},
				showTime1 : "",						//
				showTime2 : "",						//
				tableHeade: [
					{name: "",    	prop: "No",      	width: 100,	tooltip: false, sortable: true},
					{name: "", 		prop: "name",    	width: 140, tooltip: false},
					{name: "",	prop: "partyBranch",width: "", 	tooltip: true},
					{name: "",   	prop: "totalCode",	width: 100, tooltip: false},
					{name: "",	prop: "rankTime",	width: 160, tooltip: false},
				],
				tableData: [
					{ No: '01', name: '', partyBranch: ' 1518 ', totalCode:"888", rankTime:"2017-12-08" },
					{ No: '02', name: '', partyBranch: ' 1518 ', totalCode:"888", rankTime:"2017-12-07" },
					{ No: '03', name: '', partyBranch: ' 1518 ', totalCode:"888", rankTime:"2017-12-06" },
					{ No: '04', name: '', partyBranch: ' 1518 ', totalCode:"888", rankTime:"2017-12-05" },
					{ No: '05', name: '', partyBranch: ' 1518 ', totalCode:"888", rankTime:"2017-12-04" },
				]
			}
		},
		methods:{
			//
			openTime(){
				
			},
			//
			search(){
				
			},
			//
			detail(index){
				this.$router.push({path: "user_detail"})
			},
			//
			remove(index){
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			},
			//
			toBlacklist(index){
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			}
		}
	})
</script>

<style scoped lang="less">
	.personal_ranking {
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


