<template>
	<div class="sign_in">
		<el-form :inline="true" :model="formInline" class="demo-form-inline">
			<el-row>
				<!---->
				<el-col :span="4">
					<el-form-item prop="date1">
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date1" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<el-col class="line"></el-col>
				<el-col :span="4">
					<el-form-item prop="date2">
						<el-date-picker size="medium" type="date" placeholder="" v-model="formInline.date2" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2">
					<el-form-item>
						<el-button size="medium" type="info" @click="search"></el-button>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="3" :offset="6">
					<el-form-item>
						<el-button type="primary" size="medium" @click="addNew"></el-button>
					</el-form-item>
				</el-col>
				<!---->
				<el-col :span="2">
					<el-form-item>
						<el-button type="warning" size="medium" @click="batchRemove"></el-button>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<br />
		<!---->
		<div class="signIn_list">
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column align="center" type="selection" width="55" fixed="left"></el-table-column>
				<el-table-column v-for="item in tableHeade" :key="item.prop" align="center" :prop="item.prop" :label="item.name" :width="item.width" :show-overflow-tooltip="item.tooltip"></el-table-column>
				<el-table-column align="center" label="" width="220" fixed="right">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click="edit(scope.$index, scope)"></el-button>
						<el-button type="text" size="small" @click="check(scope.$index)"></el-button>
						<el-button type="text" size="small" @click="delet(scope.$index)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<!---->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"  :page-size="100" layout="prev, pager, next, jumper" :total="1000"> </el-pagination>
	</div>
</template>

<script>
	export default({
		name: "sign_in",
		data(){
			return{
				formInline:{
					date1: "",
					date2: "",
				},
				tableHeade: [
					{name: "",  	prop: "name",     	width: 120,	tooltip: false},
					{name: "", 	prop: "time",   	width: 160,	tooltip: false},
					{name: "", 	prop: "partyBranch",width: "", 	tooltip: true},
					{name: "", 	prop: "totalNum", 	width: 120, tooltip: false},
				],
				tableData: [
					{ name: 77, time: '2016-05-03', partyBranch: '', totalNum: 1123 },
					{ name: 77, time: '2016-05-03', partyBranch: '', totalNum: 1123 },
					{ name: 77, time: '2016-05-03', partyBranch: '', totalNum: 1123 },
				],
			}
		},
		methods:{
			//
			search() {
				console.log('search!');
			},
			//
			addNew(){
				this.$router.push({path: "/sign_in_add"})
			},
			//
			handleSelectionChange(val) {
        		console.log(val)
      		},
			//
			batchRemove(){
				var that = this;
				that.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
				}).catch(()=>{})
			},
			//
			edit(index,scope){
				console.log("",index, scope)
				this.$router.push({path: "/sign_in_add", query:{reName: ""}})
			},
			//
			check(index){
				console.log("",index)
				this.$router.push({path: "sign_in_check"})
			},
			//
			delet(index){
				console.log("",index)
				var that = this;
				console.log("",index)
				that.$confirm('', '', {
		          	confirmButtonText: '',
		          	cancelButtonText: '',
		          	type: 'warning'
		        }).then(() => {
		          	that.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		        }).catch(() => {});
			},
			handleSizeChange(val) {
        		console.log(`: ${val}`);
      		},
      		handleCurrentChange(val) {
        		console.log(`: ${val}`);
      		}
		}
	})
</script>

<style scoped="scoped" lang="less">
	.sign_in{
		.line {
			margin-right: 10px;
			height: 40px;
			width: 20px;
			text-align: center;
			line-height: 40px;
		}
	}
</style>


